import React, { useState }  from "react";

import Drawer from "@material-ui/core/Drawer";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import SearchIcon from "@material-ui/icons/Search";
import MenuIcon from "@material-ui/icons/Menu";
import TextField from "@material-ui/core/TextField";
import { makeStyles } from "@material-ui/core/styles";

import MyTree from "./MyTree";

const drawerStyle = makeStyles({
  paper: {
    width: () => {
      let w = window.innerWidth > 1024 ? 300 : "80vw"
      return w;
    },
    display: "flex",
    flexDirection: "column",
    padding: 30,
    boxSizing: "border-box",
  }
});
const buttonStyle = makeStyles({
  root: {
    position: "fixed",
    top: "1vh",
    right: "1vh",
    zIndex: 20000
  }
});


const Nav = (props) => {
  const [drawer, setDrawer] = useState(false);
  const [tree, setTree] = useState(props.tree);

  const drawerClasses = drawerStyle();
  const buttonClasses = buttonStyle();

  const handleChange = (e) => {
    const treeItems = document.querySelectorAll(".MuiTreeItem-root");
    let regex = new RegExp(e.target.value, "ig");

    for(let i = 0; i < treeItems.length; i++) {
      let item = treeItems[i],
        str = item.textContent.replace(" ", "");
      if(str.match(regex) !== null) {
        item.classList.remove("treeitem--disable");
      }else {
        item.classList.add("treeitem--disable");
      }
    };
  }

  return (
    <section>
      <IconButton 
        classes={buttonClasses}
        onClick={() => {drawer ? setDrawer(false) : setDrawer(true)}}
      >
        <MenuIcon />
      </IconButton>

      <Drawer classes={drawerClasses} anchor="right" open={drawer} onClose={() => {setDrawer(false)}}>
        
          <TextField onChange={handleChange} label="Search"/>

        <MyTree tree={tree} handleClick={props.handleClick}/>
      </Drawer>
    </section>
  );
}


export default Nav;
