import React from "react";
import TreeView from "@material-ui/lab/TreeView";
import TreeItem from "@material-ui/lab/TreeItem";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ChevronRightIcon from "@material-ui/icons/ChevronRight";

import { makeStyles, styled } from "@material-ui/core/styles"

const useStyle = makeStyles({
  content: {
    color: "#00008b"
  }
});

const LastChild = styled(TreeItem)({
  color: "#00008b"
});

const renderTree = (data, handleClick) => {
  let stack = [];

  if(data.nodes) {
    for(let i = 0; i < data.nodes.length; i++) {
      let item = data.nodes[i];
      if(item.nodes) {
        stack.push(renderTree(item));
      }else {
        stack.push((
          <LastChild
            onClick={handleClick}
            label={item.name}
            nodeId={item.id}
            key={item.id}
            />
        ));
      }
    }
  }

  return (
    <TreeItem label={data.name} nodeId={data.id} key={data.id}>
      {stack}
    </TreeItem>
  );
}


const MyTree = (props) => {
  const treeItems = props.tree.map(item => renderTree(item, props.handleClick));
  
  return (
    <TreeView
      defaultCollapseIcon={<ExpandMoreIcon />}
      defaultExpandIcon={<ChevronRightIcon />}
      defaultExpanded={props.tree.map(item => item.id)}
    >
      {treeItems}
    </TreeView>
  );
};

export default MyTree;
