const App = () => {
const [tabularData, setTabularData] = useState(obj1);
const [page, setPage] = useState(0);
const [rowsPerPage, setRowsPerPage] = useState(20);
const [orderBy, setOrderBy] = useState(null);

// event listener for navigation buttons
const handleClick = (e) => {
  // set query
  let query = e.target.textContent.replace(" ", "");

  // get new Data
  let data = query == "child2-1" ? obj2 : obj1;

  // set new data in state
  setTabularData(data);
  setPage(0);
}

// event listener for sorting data depend on head
  const handleSorting = (e) => {
    let sorted = Object.assign({}, tabularData),
      order = {};
    sorted.body = e.target.TextContent == orderBy ? sorted.body : sorted.body.reverse();

    setTabularData(sorted);
    setOrderBy(e.target.textContent);
  }
  const handleChangePage = (e, newPage) => {
    setPage(newPage);
  }
  const handleChangeRowsPerPage = (e) => {
    setRowsPerPage(parseInt(e.target.value));
    setPage(0);
  }

  return (
    <section>
      <Nav tree={data} handleClick={handleClick}/>
      <CustomTable
        handleSorting={handleSorting}
        orderBy={orderBy}
        data={tabularData}
        page={page}
        rowsPerPage={rowsPerPage}
        handleChangeRowsPerPage={handleChangeRowsPerPage}
        handleChangePage={handleChangePage}/>
    </section>
  );
};


export default App;
