import React, { useState } from "react";

import { makeStyles } from "@material-ui/core/styles";

import Table from "@material-ui/core/Table";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import TableRow from "@material-ui/core/TableRow";
import TablePagination from "@material-ui/core/TablePagination";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import FilterListIcon from "@material-ui/icons/FilterList";
import Toolbar from "@material-ui/core/Toolbar";

import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import TextField from "@material-ui/core/TextField";
import SearchIcon from "@material-ui/icons/Search";
import Checkbox from "@material-ui/core/Checkbox";

const toolbarStyles = makeStyles({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between"
  }
});
const headStyle = makeStyles({
  root: {
    background: "#727272",
    textTransform: "capitalize"
  }
});
const oddStyles = makeStyles({
  root: {
    background: "#eee"
  }
});
const evenStyles = makeStyles({
  root: {
    background: "#ccc",
  }
});

const tableStyles = makeStyles({
  root: {
    height: "80vh",
    width: "90vw",
    margin: "0 auto",
  }
});

const CustomTable = (props) => {
  const tableClasses = tableStyles();
  const headClasses= headStyle();
  const toolbarClasses = toolbarStyles();
  const oddClasses = oddStyles();
  const evenClasses = evenStyles();
  
  const [filter, setFilter] = useState(false);
  const [input, setInput] = useState("");
  const [selected, setSelected] = useState([]);

  const handleChange = (e) => {
    setInput(e.target.value)
  }
  const filterHandler = () => {
    let stack = !filter;
    setFilter(stack);
  }
  const handleSelectAll = (e) => {
    if(e.target.checked) {
      setSelected(props.data.body);
      return;
    }
    setSelected([]);
  }
  const handleSingleSelect = (row) => { 
    let newSelected = [...selected],
      idx = newSelected.indexOf(row);

    if(idx == -1) {
      newSelected.push(row);
    }else {
      newSelected = newSelected.slice(0, idx).concat(newSelected.slice(idx + 1));
    }

    setSelected(newSelected);
  }

  return (
    <TableContainer classes={tableClasses} elevation={5} component={Paper}>

      <Toolbar classes={toolbarClasses}>
        <Typography variant="h5" color="inherit" component="div">
          Custom Table
        </Typography>
        <Tooltip title="filter list">
          <IconButton onClick={filterHandler} aria-label="filter list">
            <FilterListIcon />
          </IconButton>
        </Tooltip>
          {filter ? (
            <form>
              <TextField value={input} onChange={handleChange} label="Filter" />
              <IconButton type="submit">
                <SearchIcon />
              </IconButton>
            </form>
          ) : null}
      </Toolbar>

      <Table>
        <TableHead classes={headClasses}>
          <TableRow>
            <TableCell>
              <Checkbox 
                checked={selected.length == props.data.body.length}
                onChange={handleSelectAll}
              />
            </TableCell>
          {props.data.head.map(title => {
            return (
              <TableCell key={title} align="center">
                <TableSortLabel
                  active={props.orderBy == title}
                  onClick={props.handleSorting}
                >
                  {title}
                </TableSortLabel>
              </TableCell>
            );
          })}
          </TableRow>
        </TableHead>
        
        <TableBody>
          {props
              .data
              .body
              .slice(
                props.page * props.rowsPerPage,
                (props.page + 1) * props.rowsPerPage)
              .map((row, index) => {
                return(
                  <TableRow
                    classes={index % 2 == 0 ? evenClasses : oddClasses}
                    key={index}
                  >
                    <TableCell>
                      <Checkbox
                        onClick={() => {handleSingleSelect(row)}}
                        checked={selected.indexOf(row) !== -1}
                      />
                    </TableCell>
                    {row.map(item => {
                      return (<TableCell key={item} align="center">{item}</TableCell>);
                    })}
                  </TableRow>
                );
          })}
        </TableBody>
      </Table>
      <TablePagination 
        component="div"
        rowsPerPageOptions={[5, 10, 20]}
        rowsPerPage={props.rowsPerPage}
        page={props.page}
        count={props.data.body.length}
        onChangeRowsPerPage={props.handleChangeRowsPerPage}
        onChangePage={props.handleChangePage}
      />
    </TableContainer>
  );
};


export default CustomTable;
